@import "../scss/variables";

@media (min-width: 1025px) {
	.page {
		max-width: 1170px;
		margin: 0 auto;
		background: #f2f3f9;
		position: relative;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column;
		flex-direction: column;
		-ms-flex-pack: center;
		justify-content: center;
		min-height: 100vh;
		box-shadow: $black-3 0px 5px 0px 0px, $black-3 0px 5px 25px 0px;
	}
	body {
		background: #f2f3f9 !important;
	}
	.sticky-pin .horizontal-main {
		box-shadow: 0 2px 8px 0 rgba(40, 37, 89, 0.2);
		background: $white;
		max-width: 1170px;
	}
	.app-header {
		width: 1170px !important;
		margin: 0 auto;
	}
	.vh {
		height: 100vh;
	}
	.app-sidebar {
		left: auto !important;
	}
	.side-header {
		left: auto !important;
		right: auto !important;
		z-index: 1044;
		background: $white;
	}
}